.input{
    display: block;
    border:1px solid transparent;
    border-radius: 5px;
    outline: none;
    outline-style: none;
    padding: 0 10px;
    height: 30px;
    line-height: 30px;
}
.input.default{
    width: 300px;
}
.input.small{
    width: 200px;
}
.input.big{
    width: 360px;
}
.group{
    display: flex;
    justify-content: center;
    align-items: center;
    border: 1px solid #ccc;
    border-radius: 5px;
    width: 600px;
}